"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import Link from "next/link";

const VideosPage = () => {
  const router = useRouter();
  const { id } = router.query;

  const [animeInfo, setAnimeInfo] = useState<any>(null);
  const [youtubeTrailers, setYoutubeTrailers] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  // 1. Carregar informa√ß√µes do anime
  useEffect(() => {
    async function loadAnimeInfo() {
      if (!id) return;
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnimeInfo(json.data);
        
        // Buscar trailers baseados no anime
        await searchTrailers(json.data);
      } catch (error) {
        console.error("Erro:", error);
      } finally {
        setLoading(false);
      }
    }
    loadAnimeInfo();
  }, [id]);

  // 2. Buscar trailers otimizados
  const searchTrailers = async (animeData: any) => {
    try {
      // URLs de busca direta no YouTube
      const trailerQueries = [
        `${animeData.title} Official Trailer`,
        `${animeData.title} PV`,
        `${animeData.title} Opening`,
        `${animeData.title} Anime Trailer`,
        `${animeData.title} CM`
      ];

      // Usar thumbnails do pr√≥prio anime
      const animeThumbnail = animeData.images?.jpg?.large_image_url || 
                            animeData.images?.jpg?.image_url ||
                            `https://placehold.co/300x169/1e88e5/white?text=${encodeURIComponent(animeData.title.substring(0, 20))}`;

      const trailers = trailerQueries.map((query, index) => ({
        id: `trailer-${index}`,
        title: query.replace(animeData.title + " ", ""), // Remove nome repetido
        url: `https://www.youtube.com/results?search_query=${encodeURIComponent(query)}`,
        thumbnail: animeThumbnail, // Usa capa do anime
        type: "trailer"
      }));

      setYoutubeTrailers(trailers);
    } catch (error) {
      console.error("Erro ao buscar trailers:", error);
    }
  };

  // 3. Extrair ID do YouTube (para futuro embed)
  const getYouTubeId = (url: string) => {
    if (!url) return null;
    const match = url.match(/(?:youtube\.com\/watch\?v=|youtu\.be\/|youtube\.com\/embed\/)([a-zA-Z0-9_-]+)/);
    return match ? match[1] : null;
  };

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/detail.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
        padding: "20px",
      }}
    >
      <div style={{ maxWidth: "1200px", margin: "0 auto" }}>
        {/* CABE√áALHO SIMPLES */}
        <div style={{ 
          display: "flex", 
          justifyContent: "space-between", 
          alignItems: "center", 
          marginBottom: 30,
          flexWrap: "wrap",
          gap: 15
        }}>
          <div>
            <h1 style={{ color: "white", margin: "0 0 5px 0" }}>
              {animeInfo?.title || "Carregando..."}
            </h1>
            <p style={{ color: "#aaa", margin: 0, fontSize: "16px" }}>
              Trailers e V√≠deos
            </p>
          </div>
          
          <Link href={`/${id}`}>
            <button style={{ 
              padding: "10px 20px", 
              background: "#333", 
              color: "white", 
              border: "none", 
              borderRadius: "5px", 
              cursor: "pointer",
              fontWeight: "bold",
              whiteSpace: "nowrap"
            }}>
              ‚Ü© Voltar
            </button>
          </Link>
        </div>

        {/* STATUS LIMPO */}
        <div style={{ 
          background: "rgba(0, 0, 0, 0.7)", 
          padding: "12px 15px", 
          borderRadius: "8px",
          marginBottom: "25px",
          color: "white",
          fontSize: "14px"
        }}>
          <p style={{ margin: 0 }}>
            <strong>Anime:</strong> {animeInfo?.title || "Carregando..."}
            {animeInfo?.year && ` (${animeInfo.year})`}
          </p>
        </div>

        {/* MENSAGEM INFORMATIVA */}
        {!loading && youtubeTrailers.length > 0 && (
          <div style={{ 
            background: "rgba(30, 136, 229, 0.15)", 
            border: "1px solid #1e88e5",
            padding: "15px", 
            borderRadius: "8px",
            marginBottom: "25px",
            color: "#90caf9",
            fontSize: "14px"
          }}>
            <p style={{ margin: 0 }}>
              üîç <strong>Trailers do YouTube:</strong> Clique em qualquer card para buscar no YouTube
            </p>
          </div>
        )}

        {/* LOADING */}
        {loading ? (
          <div style={{ 
            textAlign: "center", 
            padding: "60px 20px",
            background: "rgba(0, 0, 0, 0.6)",
            borderRadius: "10px"
          }}>
            <div style={{ 
              width: "50px", 
              height: "50px", 
              border: "5px solid #333",
              borderTop: "5px solid #1e88e5",
              borderRadius: "50%",
              margin: "0 auto 20px",
              animation: "spin 1s linear infinite"
            }} />
            <p style={{ color: "white", fontSize: "18px" }}>
              Buscando trailers...
            </p>
          </div>
        ) : youtubeTrailers.length === 0 ? (
          <div style={{ 
            textAlign: "center", 
            padding: "40px",
            background: "rgba(0, 0, 0, 0.6)",
            borderRadius: "10px"
          }}>
            <p style={{ color: "white", fontSize: "18px", marginBottom: "15px" }}>
              Nenhum trailer encontrado.
            </p>
            <button 
              onClick={() => animeInfo && searchTrailers(animeInfo)}
              style={{
                padding: "10px 20px",
                background: "#1e88e5",
                color: "white",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
                fontWeight: "bold"
              }}
            >
              üîÑ Tentar Novamente
            </button>
          </div>
        ) : (
          <div style={{ 
            display: "grid", 
            gridTemplateColumns: "repeat(auto-fill, minmax(300px, 1fr))", 
            gap: 20
          }}>
            {youtubeTrailers.map((trailer, index) => {
              const youtubeId = getYouTubeId(trailer.url);
              const hasYouTubeLink = youtubeId ? `https://youtube.com/watch?v=${youtubeId}` : trailer.url;
              
              return (
                <a
                  key={trailer.id}
                  href={hasYouTubeLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{ 
                    textDecoration: 'none', 
                    color: 'inherit', 
                    display: 'block',
                    background: "rgba(0, 0, 0, 0.85)", 
                    borderRadius: 10, 
                    overflow: "hidden",
                    border: "1px solid rgba(255, 255, 255, 0.15)",
                    transition: "all 0.3s ease",
                    cursor: "pointer"
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.transform = "translateY(-5px)";
                    e.currentTarget.style.boxShadow = "0 10px 25px rgba(0, 0, 0, 0.5)";
                    e.currentTarget.style.borderColor = "#1e88e5";
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.transform = "translateY(0)";
                    e.currentTarget.style.boxShadow = "none";
                    e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.15)";
                  }}
                >
                  {/* THUMBNAIL COM CAPA DO ANIME */}
                  <div style={{ position: "relative", paddingBottom: "56.25%" }}>
                    <img
                      src={trailer.thumbnail}
                      alt={trailer.title}
                      style={{
                        position: "absolute",
                        top: 0,
                        left: 0,
                        width: "100%",
                        height: "100%",
                        objectFit: "cover"
                      }}
                    />
                    
                    {/* OVERLAY ESCURO PARA TEXTO LEG√çVEL */}
                    <div style={{
                      position: "absolute",
                      bottom: 0,
                      left: 0,
                      right: 0,
                      background: "linear-gradient(to top, rgba(0,0,0,0.9), transparent)",
                      padding: "15px"
                    }}>
                      <h3 style={{ 
                        color: "white", 
                        margin: 0,
                        fontSize: "16px",
                        lineHeight: "1.4"
                      }}>
                        {trailer.title}
                      </h3>
                    </div>
                    
                    {/* BOT√ÉO PLAY */}
                    <div style={{
                      position: "absolute",
                      top: "50%",
                      left: "50%",
                      transform: "translate(-50%, -50%)",
                      background: "rgba(255, 0, 0, 0.85)",
                      width: "60px",
                      height: "60px",
                      borderRadius: "50%",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      fontSize: "24px",
                      color: "white",
                      border: "3px solid white"
                    }}>
                      ‚ñ∂
                    </div>
                  </div>
                  
                  {/* RODAP√â DO CARD */}
                  <div style={{ 
                    padding: "15px",
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center"
                  }}>
                    <span style={{
                      background: "#1e88e5",
                      color: "white",
                      padding: "5px 12px",
                      borderRadius: "20px",
                      fontSize: "12px",
                      fontWeight: "bold",
                      display: "flex",
                      alignItems: "center",
                      gap: "5px"
                    }}>
                      <span style={{ fontSize: "14px" }}>üé¨</span> Trailer
                    </span>
                    
                    <span style={{
                      color: "#aaa",
                      fontSize: "12px",
                      display: "flex",
                      alignItems: "center",
                      gap: "5px"
                    }}>
                      <span>üîó</span> YouTube
                    </span>
                  </div>
                </a>
              );
            })}
          </div>
        )}

        {/* ESTILO PARA ANIMA√á√ÉO DE LOADING */}
        <style jsx>{`
          @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
        `}</style>
      </div>
    </div>
  );
};

export default VideosPage;